.nav {
  position: relative;
  background-color: #00adb5;
  /* animation: bcg 5s linear infinite alternate; */
}
@keyframes bcg {
  0% {
    background-color: rgb(129, 35, 207);
  }
  20% {
    background-color: rgb(87, 35, 207);
  }
  40% {
    background-color: rgb(35, 190, 207);
  }
  60% {
    background-color: rgb(35, 204, 207);
  }
  80% {
    background-color: rgb(207, 35, 158);
  }
  100% {
    background-color: rgb(207, 35, 35);
  }
}

.navItems {
  padding-top: 10px;
}
.navItems a {
  min-width: 100px;
  margin: 10px;
  padding: 5px;
  border-radius: 50px;
  cursor: pointer;
  color: #eeeeee;
  background-color: #393e46;
  transition: 1s all;
}
.navItems a:hover {
  background-color: #222831;
}

.navBtnsOn,
.navBtnsOff {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 50%;
  margin: 0 auto;
  border-radius: 50px;
  background-color: #393e46;
  transition: 1.5s all;
}
.navBtnsOn {
  opacity: 1;
}
.navBtnsOff {
  opacity: 0;
}
.navBtnsOn a {
  opacity: 1;
}
.navBtnsOff a {
  opacity: 0;
  margin: 0;
}

.menuBtn {
  font-size: 25px;
  transition: 1s;
  cursor: pointer;
}
.menuBtn:hover {
  scale: 1.2;
}
