.card {
  max-width: 300px;
  margin: 10px;
  padding: 10px;
  border: 3px solid black;
  border-radius: 30px;
  box-shadow: 0px 5px 10px 2px rgba(15, 21, 21, 0.4);
  background-color: #eeeeee;
}

h3 {
  margin-bottom: 5px;
}

p {
  text-align: justify;
}

img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
}
